

$sideBarColor: #000000;
$sideBarBg: #ffffff;
$compoBgColor: #eee ;
$svgFill:#9e2153;
$svgFillHover: #d1325a;

#root{
  width: 100vw;
  padding: 0;
  margin: 0;
}

@mixin pmzero {
  padding: 0;
  margin: 0;
}

@mixin vhcenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin circle($widthPassed, $heightPassed){
  overflow: hidden;
  border-radius: 50%;
  width: $widthPassed;
  height: $heightPassed;
}

@mixin whfull{
  width: 100%;
  height: 100%;
}

@mixin textEllipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
