@import './Base.scss';
 
.compenyLogo{
  width: 100%;
  height: 3em;
  box-shadow: 0 0 20px 9px #0000001a;
  padding: 0 .3em 0 .3em;
  img{
    width: 100%;
    height: 100%;
  }
}

.sideBar{
  height: 100vh;
  overflow: hidden;
  color:$sideBarColor;
  box-shadow:8px 0 20px 1px #00000026;
  position: fixed;
  width: 0;
  padding:0;
  background-color: $sideBarBg;
  transition: all .3s linear;
}

.sidebarItems{
  margin-top: 1em;
  .sidebarChild{
    width: 100%;
    padding: .5em;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: all .3s linear;
    &:hover{
      background-color: rgba(255,255,255,.4);
    }
    .sidebarSvg{
      fill:$svgFill;
      &:hover{
        fill:$svgFillHover;
      }
    }
    .sidebarText{
      @include textEllipsis;
      padding-left:.4em;
    }
  }
}

.showMenu{
  @include vhcenter;
  padding-left: 1em;
  width: 25%;
  justify-content: flex-start;
  svg{
    display: none;
    cursor: pointer;
    fill:$svgFill;
    &:hover{
      fill:$svgFillHover;
    }
  }
  img{
    width: 45%;
    height: 2.5em;
  }
}
.hideSideBar{
  display: block;
  padding:.5em .5em 0 0;
  svg{
    float: right;
    fill: $svgFill;
    &:hover{
      fill: $svgFillHover;
    }
  }
}
@media screen and (max-width: 768px){
  .sideBar{
    width: 0;
    padding: 0;
  }
  .sideBarView{
    width: 12em;
    padding:.2em;
  }
  .showMenu{
    svg{
      display: block;
    }
    img{
      display: none;
    }
  }
}
