@import './Base.scss';
@import './Sidebar.scss';
@import './Basic.scss';

.mainCont{
  height: 100vh;
  overflow: auto;
  padding-left: 0;
  width: -webkit-fill-available;
}
.menuSection{
  width: 50%;
  @include vhcenter;
}
.menuItem{
  padding:1em;
}
.headerMenuCont{
  border-bottom:1px solid #00000029;
  background-color:$compoBgColor ;
  box-shadow: 0 0 20px 9px #0000001a;
  height: 4em;
  align-items: center;
  display: flex;
}
.userDetail{
  width: 25%;
  @include vhcenter;
  justify-content: flex-end;
  padding-right: 1em;
}
.userName{
  text-overflow: ellipsis;
  word-break: normal;
  padding: 0 .4em 0 .4em;
  line-height: .9;
}
.userImage{
  @include circle(40px, 40px);
}
.userImage > img {
  @include whfull;
}

.menuItem{
  cursor: pointer;
  transition: all .3s linear;
  &:hover{
    color:red;
  }
}
.userLogout{
  cursor: pointer;
  padding: 0 .4em 0 .4em;
  fill:$svgFill;
  &:hover{
    fill: $svgFillHover;
  }
}
.workSection{
  background-color: darken( $compoBgColor, 10% );;
  height: inherit;
}
.companyLogo{
  width: 100%;
  @include vhcenter;
  img{
    width: 70%;
    height: 2.7em;
  }
  display:none;
}
@media screen and (max-width: 768px){
  .companyLogo{
    display: flex;
  }
  .mainCont{
    padding: 0;
  }
  .menuItem{
    display: none;
  }
  .userName{
    display: none;
  }
  .userImage{
    @include circle(40px, 40px);
  }
}